<template>
  <div class="pulltorefresh h-100" :class="{'touching':touching}">
    <div class="touch-indicator" :style="translation">
      <v-progress-circular color="primary" :indeterminate="refreshing" size="30"></v-progress-circular>
    </div>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "pull-to-refresh",
  props: {
    disabled: {
      default: () => false
    },
    refreshFn: {
      type: Function,
      required: false,
      default: () => {
        return () => {
          console.log("Refreshing...");
        };
      }
    }
  },
  data: () => ({
    refreshing: false,
    touching: false,
    pStart: { x: 0, y: 0 },
    pCurrent: { x: 0, y: 0 }
  }),
  mounted() {
    this.putListeners();
  },
  beforeDestroy() {
    this.removeListeners();
  },
  computed: {
    translation: {
      get: function() {
        // let delta = this.pCurrent.y - this.pStart.y;
        // if (delta > 30) {
        //   delta = 30;
        // }
        // return `transform: translateY(${10}px)`;
        return "";
      }
    }
  },
  methods: {
    putListeners() {
      this.$el.addEventListener("touchstart", e => this.swipeStart(e), false);
      this.$el.addEventListener("touchmove", e => this.swipe(e), false);
      this.$el.addEventListener("touchend", e => this.swipeEnd(e), false);
    },
    removeListeners() {
      this.$el.removeEventListener(
        "touchstart",
        e => this.swipeStart(e),
        false
      );
      this.$el.removeEventListener("touchmove", e => this.swipe(e), false);
      this.$el.removeEventListener("touchend", e => this.swipeEnd(e), false);
    },
    swipeStart(e) {
      if (this.refreshing || this.disabled) {
        return;
      }
      if (typeof e["targetTouches"] !== "undefined") {
        let touch = e.targetTouches[0];
        this.pStart.x = touch.screenX;
        this.pStart.y = touch.screenY;
      } else {
        this.pStart.x = e.screenX;
        this.pStart.y = e.screenY;
      }
    },
    swipeEnd() {
      if(this.disabled){
        return;
      }
      let changeY =
        this.pStart.y < this.pCurrent.y
          ? Math.abs(this.pStart.y - this.pCurrent.y)
          : 0;
      //const rotation = changeY < 100 ? (changeY * 30) / 100 : 30;
      if (changeY > 100) {
        this.doRefresh();
      } else {
        this.touching = false;
      }
      this.pStart = { x: 0, y: 0 };
      this.pCurrent = { x: 0, y: 0 };
    },
    swipe(e) {
      if (this.refreshing || this.disabled) {
        return;
      }
      if (typeof e["changedTouches"] !== "undefined") {
        let touch = e.changedTouches[0];
        this.pCurrent.x = touch.screenX;
        this.pCurrent.y = touch.screenY;
      } else {
        this.pCurrent.x = e.screenX;
        this.pCurrent.y = e.screenY;
      }
      let changeY =
        this.pStart.y < this.pCurrent.y
          ? Math.abs(this.pStart.y - this.pCurrent.y)
          : 0;
      this.touching = true;
      if (changeY > 5) {
        this.touching = true;
      } else {
        this.touching = false;
      }
    },
    async doRefresh() {
      try {
        //this.refreshing = true;
        this.refreshing = true;
        await this.refreshFn();
        this.setTimeout(() => {
          this.refreshing = false;
          this.touching = false;
        }, 500);
      } catch {
        this.refreshing = false;
        this.touching = false;
      }
    }
  }
};
</script>