import Vue from 'vue'
import VueI18n from 'vue-i18n'


Vue.use(VueI18n)

const navigatorLocale = navigator.language || navigator.userLanguage;
let defaultLocale = navigatorLocale.split('-')[0];
const availableLocales = require.context('./locales', true, /[A-Za-z0-9-_,\s]+\.json$/i)


function loadLocaleMessages() {

    const messages = {}
    availableLocales.keys().forEach(key => {
        const matched = key.match(/([A-Za-z0-9-_]+)\./i)
        if (matched && matched.length > 1) {
            const locale = matched[1]
            messages[locale] = availableLocales(key)
        }
    })
    return messages
}

export default new VueI18n({
    locale: defaultLocale || process.env.VUE_APP_I18N_LOCALE || 'en',
    fallbackLocale: process.env.VUE_APP_I18N_FALLBACK_LOCALE || 'en',
    messages: loadLocaleMessages()
})