<template>
  <v-app-bar app color="white" elevation="1" flat :hide-on-scroll="false" class="app-bar">
    <div class="content">
      <div class="backBtn" v-if="showBack">
        <v-btn icon @click="goBack">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </div>
      <div class="title">
        <h3 class="username">{{ $t('hi', {name:user.firstName}) }}</h3>
        <small>{{ $tc('you.project.count', projects.length) }}</small>
      </div>
      <div class="avatar">
        <router-link :to="{name:'settings'}">
          <v-avatar color="primary" class="border-primary" size="32">
            <span class="white--text" style="font-size:.9rem">{{ user.initials }}</span>
          </v-avatar>
        </router-link>
      </div>
    </div>
  </v-app-bar>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters({
      user: "auth/user",
      projects: "project/all",
      showBack: "app/show_back"
    })
  },
  mounted() {
    document.body.classList.add("has-app-bar");
  },
  beforeDestroy() {
    document.body.classList.remove("has-app-bar");
  },
  methods: {
    goBack() {
      this.$router.replace({
        name: "projects"
      });
    }
  }
};
</script>