import {
    IDEA_ISSUE
} from '../graphql';
import {
    IdeaIssue
} from '../models';
// import { queryToPromise } from '../lib/utils';
// import { apolloClient } from '../plugins/apollo/client';

const initialState = () => {
    return {
        loading: false,
        all: [],
        filter: null,
    }
};

const state = initialState();



const getters = {
    loading: state => state.loading,
    all: state => state.all.sort((a, b) => (a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1)),
    filter: state => state.filter,
    filteredItems: state => state.filter && state.filter.length > 3 ? state.all.filter(i => i.name.toLowerCase().includes(state.filter.toLowerCase())) : state.all,
};


const actions = {
    async create(context, form) {
        const result = await form.mutate({
            mutation: IDEA_ISSUE.create,
            /*update(cache, result) {
                const { ideaFindAll } = cache.readQuery({ query: IDEA.findAll });
                cache.writeQuery({
                    query: IDEA.findAll,
                    data: { ideaFindAll: ideaFindAll.concat([result.data.ideaCreate]) },
                });
            }*/
        });
        const item = new IdeaIssue().deserialize(result.data.ideaIssueCreate);
        //await context.dispatch('findAll', { force: true });
        return item;
    },

};

const mutations = {

}

export default {
    namespaced: true,
    state: state,
    getters: getters,
    actions: actions,
    mutations: mutations
}