<template>
  <div class="header-card pt-5">
    <div class="header center" style="text-align:center">
      <!-- <v-avatar color="white" class="border mb-4" size="90">
        <span class="primary--text headline">{{ user.initials }}</span>
      </v-avatar>-->
      <div>
        <span
          class="d-block text-uppercase"
          style="font-size:1.4em;font-weight:600"
        >{{ user.fullName }}</span>
        <span
          class="d-block primary--text text-uppercase"
          style="font-size:1em;font-weight:600"
        >{{ user.companyRole.name }}</span>
      </div>
    </div>
    <div class="content px-0">
      <v-form @submit.prevent="save" @keyup="$validator.validateAll()">
        <v-container class fluid>
          <v-row>
            <v-col cols="12" class="px-0 py-0">
              <v-text-field
                v-model="user.company.name"
                class="inline inner"
                :label="$t('Company')"
                readonly
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="px-0 py-0">
              <v-text-field
                id="firstName"
                :disabled="form.busy"
                v-model="form.firstName"
                :label="$t('First name')"
                type="text"
                class="inline inner"
                name="firstName"
                :error="$validateState('firstName', form)"
                :error-messages="$displayError('firstName', form)"
                v-validate="'required|min:4'"
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="px-0 py-0">
              <v-text-field
                id="lastName"
                :disabled="form.busy"
                v-model="form.lastName"
                :label="$t('Last name')"
                class="inline inner"
                type="text"
                name="lastName"
                :error="$validateState('lastName', form)"
                :error-messages="$displayError('lastName', form)"
                v-validate="'required|min:4'"
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="px-0 py-0">
              <v-text-field
                id="email"
                :disabled="form.busy"
                v-model="form.email"
                :label="$t('Email')"
                class="inline inner"
                type="email"
                name="email"
                :error="$validateState('email', form)"
                :error-messages="$displayError('email', form)"
                v-validate="'required|email'"
              ></v-text-field>
            </v-col>
            <v-col cols="12 px-0 py-0">
              <v-expansion-panels class="no-borders">
                <v-expansion-panel expand>
                  <v-expansion-panel-header class="text-uppercase">{{ $t('Password') }}</v-expansion-panel-header>
                  <v-expansion-panel-content class="px-0">
                    <v-checkbox
                      class="inner"
                      v-model="form.changePassword"
                      :label="$t('Change password')"
                    ></v-checkbox>
                    <v-text-field
                      id="password"
                      :disabled="form.busy || !form.changePassword"
                      v-model="form.password"
                      :label="$t('Password')"
                      class="inline inner"
                      type="password"
                      name="password"
                      ref="password"
                      :error="$validateState('password', form)"
                      :error-messages="$displayError('password', form)"
                      v-validate="{ required: this.form.changePassword, min:4 }"
                    ></v-text-field>
                    <v-text-field
                      id="passwordConfirmation"
                      :disabled="form.busy || !form.changePassword"
                      v-model="form.passwordConfirmation"
                      :label="$t('Password confirmation')"
                      class="inline inner"
                      type="password"
                      name="passwordConfirmation"
                      ref="password"
                      :error="$validateState('passwordConfirmation', form)"
                      :error-messages="$displayError('passwordConfirmation', form)"
                      v-validate="{ required: this.form.changePassword, min:4, confirmed:'password' }"
                    ></v-text-field>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
            <v-col cols="12" class="pt-6">
              <v-btn
                color="primary"
                block
                rounded
                :loading="form.busy"
                :disabled="vErrors.any() || form.busy"
                class="text-uppercase"
                type="submit"
              >{{ $t('Save changes') }}</v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
      <v-divider />
      <v-container class="py-4 px-8 text-center" fluid>
        <v-btn
          @click.stop="logout"
          color="danger"
          class="mx-3"
          rounded
          outlined
        >{{ $t('Sign out') }}</v-btn>

        <v-menu top offset-y>
          <template v-slot:activator="{ on }">
            <v-btn
              color="primary"
              class="mx-3 text-uppercase"
              rounded
              v-on="on"
              outlined
            >{{ form.lang }}</v-btn>
          </template>

          <v-list>
            <v-list-item hover v-for="(item, index) in serverConfig.langs" :key="index">
              <v-list-item-title
                style="font-size:12px;font-weight:600"
                class="text-uppercase"
                @click="()=>changeLang(item.code)"
              >{{item.code}}: {{ item.nativeName }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-container>
    </div>
  </div>
</template>
<script>
import { /*mapState,*/ mapGetters } from "vuex";
import GQLForm from "@/lib/gqlform";
import { blockUi } from "@/lib/utils";
export default {
  data: () => ({
    form: new GQLForm({
      file: null,
      firstName: null,
      lastName: null,
      email: null,
      changePassword: false,
      password: null,
      passwordConfirmation: null,
      deleteAvatar: null,
      lang: null
    })
  }),
  computed: {
    ...mapGetters({
      user: "auth/user",
      serverConfig: "app/serverConfig"
    })
  },
  mounted() {
    this.initForm();
  },
  methods: {
    async initForm() {
      Object.keys(this.user)
        .filter(key => key in this.form)
        .forEach(key => (this.form[key] = this.user[key]));
    },
    async logout() {
      blockUi();

      setTimeout(async () => {
        await this.$router.replace({
          name: "projects"
        });
        this.$store.dispatch("auth/logout");
      });
    },
    async changeLang(lang) {
      this.form.lang = lang;
      await this.save();
    },
    async save() {
      await this.$validator.validateAll();
      if (!this.vErrors.any()) {
        await this.$validator.reset();
        await this.$store.dispatch("auth/updateProfile", this.form);
        this.initForm();
      }

      /*try {
        this.form.errors = null;
        await this.$store.dispatch("auth/login", this.form);
        console.log(this.$store.getters["app/intented_route"]);
        await this.$router.replace(
          this.$store.getters["app/intented_route"] || "/"
        );
      } catch (ex) {
        //console.log(processGraphQLErrors(ex));
      } finally {
        //this.$validator.reset();
      }*/
    }
  }
};
</script>