import gql from 'graphql-tag';
import {
    META_FRAGMENT
} from './meta';
export const PROJECT_EVALUATION_RECORD = gql `
    fragment projectEvaluationRecordFields on ProjectEvaluationRecord{
        id 
        status
        projectId
        evaluationInstanceId
        ideaId
        moneyUnit
        moneyValue
        timeUnit
        timeValue
        totalValue
        idea{
            id
            title 
            process{
                id
                title
            }
            
        }
    }
`

export const PROJECT_FRAGMENT = gql `
    fragment projectFields on Project{
      id,
      name,
      description,
      createdAt,
      updatedAt,
      status,
      processId,
      process{
          id
          title,
          stages{
              id
              title
              operations{
                  id
                  title
                  phases{
                      id
                      title
                  }
              }
          }
      }
      userPendingEvaluations{
          id
          status
          userRecords{
              ...projectEvaluationRecordFields
      }
    }
      

      _metadata{
          ...metaFields
      }
    }
    ${META_FRAGMENT}
    ${PROJECT_EVALUATION_RECORD}
`;

export const PROJECT_FULL_FRAGMENT = gql `
    fragment projectFullFields on Project{
        ...projectFields,
        userIdeas{
            id,
            title,
            projectId,
            stageId,
            idea{
                id
                title,
                description,
                processId,
                createdAt,
                updatedAt
                process{
                    id
                    title,
                    id
                }
                parent{
                    __typename,
                    ... on ProcessStage{
                        id,
                        title,
                        dOrder,
                        description
                        processId
                        
                    }
                    ... on ProcessOperation{
                        id,
                        title,
                        dOrder
                        description,
                        processId
                        stageId
                        stage{
                            id
                        title
                        }
                    }
                    ... on ProcessPhase{
                        id,
                        title,
                        dOrder
                        description,
                        processId
                        operationId
                        operation{
                            id
                            title
                            stageId
                            stage{
                                id
                                title
                            }
                        }
                    }
                }
                files{
                    id
                    url
                }
            }
        }
    }
    ${PROJECT_FRAGMENT}
`;



export const PROJECT = {
    projectUpdated: gql `
        subscription projectUpdated{
            projectUpdated{
                ...projectFullFields
            }
        }
        ${PROJECT_FULL_FRAGMENT}
    `,

    newNotification: gql `
subscription newNotification{
    newNotification{
        type
        payload
    }
}
`,

    projectFindMyAll: gql `
        query projectFindMyAll($filter:Filter){
            projectFindMyAll(filter:$filter){
                ...projectFields
            }
        }
        ${PROJECT_FRAGMENT}
    `,
    findById: gql `
   query projectFindById($id:ID!){
     projectFindById(id:$id){
             ...projectFullFields
         }
     }
     ${PROJECT_FULL_FRAGMENT}
 `,
    projectEvaluateIdea: gql `
 mutation projectEvaluateIdea($input:ProjectEvaluationRecordInput!){
    projectEvaluateIdea(input:$input){
         ...projectEvaluationRecordFields
     }
 }
 ${PROJECT_EVALUATION_RECORD}
`,

}