<template>
  <div class="h-100 layout" v-if="user">
    <app-bar v-if="!hideTopbar"></app-bar>
    <v-content class="h-100">
      <sweepable @sweep="handleSweep">
        
        <slot />
      </sweepable>
    </v-content>
    <!-- <v-bottom-navigation
      app
      grow
      hide-on-scroll
      :height="45"
      color="primary"
      class="app-bottom-bar"
    >
      <v-btn :to="{name:'projects'}" style="margin:0!important;max-width:unset">
        <v-icon>mdi-picture-in-picture-top-right</v-icon>
      </v-btn>

      <v-btn :to="{name:'settings'}" style="margin:0!important;max-width:unset">
        <v-avatar color="transparent" class="border-white" size="25">
          <span class="white--text" style="font-size:.7em">{{ user.initials }}</span>
        </v-avatar>
      </v-btn>
    </v-bottom-navigation> -->
  </div>
  <div class="h-100" v-else>
    <page-loader></page-loader>
  </div>
</template>
<script>
import AppBar from "@/components/AppBar";
import { mapGetters } from "vuex";
export default {
  name: "DefaultLayout",
  components: {
    "app-bar": AppBar
  },
  computed: {
    ...mapGetters({
      hideTopbar: "app/hide_topbar",
      user: "auth/user"
    })
  },
  mounted() {
    document.body.classList.add("default-layout");
    document.body.classList.remove("external-layout");
  },
  beforeDestroy() {
    document.body.classList.remove("default-layout");
  },
  methods: {
    handleSweep(direction) {
      if (direction === "left") {
        if (this.$route.name === "projects") {
          this.$router.push({
            name: "settings"
          });
        }
      } else if (direction == "right") {
        if (this.$route.name === "settings") {
          this.$router.push({
            name: "projects"
          });
        }
      }
    }
  }
};
</script>